/* Footer 样式 */
footer {
    background-color: #2c3e50; /* 深蓝色背景 */
    color: white;
    padding: 40px 20px;
    font-size: 14px;
    text-align: center;
  }
  
  /* 第一行三列布局 */
  .footer-row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 20px;
  }
  
  .footer-column {
    flex: 1;
    margin: 0 20px;
    text-align: left;
  }
  
  .footer-column h3 {
    margin-bottom: 10px;
    font-size: 18px;
  }
  
  .footer-column p, .footer-column ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  
  .footer-column ul li {
    margin-bottom: 10px;
  }
  
  .footer-column ul li a {
    color: white;
    text-decoration: none;
    transition: color 0.3s;
  }
  
  .footer-column ul li a:hover {
    color: #1abc9c; /* 悬停时变为浅绿色 */
  }
  
  /* 第二行备案信息 */
  .footer-row:last-child {
    text-align: center;
    margin-top: 20px;
    font-size: 12px;
    color: #bdc3c7; /* 灰色备案信息 */
  }
  
  /* 适应小屏幕 */
  @media (max-width: 768px) {
    .footer-row {
      flex-direction: column;
      align-items: center;
    }
  
    .footer-column {
      margin-bottom: 20px;
      text-align: center;
    }
  }
  
  .footer-row:last-child a {
    color: #bdc3c7; /* 灰色文字颜色 */
    text-decoration: none;
  }
  
  .footer-row:last-child a:hover {
    color: #1abc9c; /* 悬停时变为浅绿色 */
    text-decoration: underline;
  }
  